.footer {
  width: 100%;
  background-color: #000;
}

.footer_container {
  position: relative;
  height: 25vh;
  display: flex;
  color: #fff;
  justify-content: space-between;
  padding: 2em;

  &_sign_up {
    display: flex;
    flex-direction: column;
    position: relative;

    h5 {
      width: 10em;
      align-self: flex-start;
    }

    &_form {
      width: 12em;
      display: flex;

      //adding slant line after form

      ::after {
        content: "";
        position: absolute;
        top: -25px;
        width: 100%;
        left: 20px;
        transform: rotate(120deg);
        transform-origin: 0;
        border-top: 1px solid #636364;
      }

      & input {
        width: 10em;
        height: 32px;
      }

      & button {
        font-size: 22px;
        //  font-weight: 700;
        width: 18px;
        color: #fff;
        background-color: #000;
        border-color: #000;
      }
    }
  }

  &_info {
    width: 25em;
    display: flex;
    justify-content: space-between;

    &_ul {
      display: block !important;
    }

    a {
      text-decoration: none;
      color: #fff;
      position: relative;
      display: flex;
      justify-content: center;
      align-self: center;
      overflow: hidden;
      padding: 3px;

      &::after {
        content: "";
        width: 100%;
        height: 100%;
        position: absolute;
        bottom: 0;
        left: 0;
        width: 0;
        height: 2px;
        background: rgb(102, 102, 100);
        transform-origin: center, right;
        transition: width 0.4s;
        margin-top: 0.5px;
      }

      &:hover::after {
        width: 100%;
      }
      &:hover {
        color: rgb(233, 227, 227);
      }
    }

    &_left {
      position: relative;

      ::after {
        content: "";
        position: absolute;
        top: -25px;
        width: 180%;
        left: 0;
        transform: rotate(120deg);
        transform-origin: 0;
        border-top: 1px solid #484849;
      }
    }
  }

  &_social_media {
    display: flex;
    flex-direction: column;
  }

  &_icons {
    display: flex;
    justify-content: center;
    align-self: center;
  }

  & h5 {
    align-self: center;
    margin-bottom: 0.8em;
    font-size: 1em;
  }

  & ul {
    display: flex;
  }

  & li {
    list-style: none;
    margin: 0 15px 0 15px;
    & svg {
      font-size: 1.3em;
    }
  }
}

.footer_base {
  display: block;
  height: auto;
  color: #fff;

  &_container {
    display: flex;
    justify-content: space-between;
    margin: 0 0.5em;
    padding: 5px;
    // width: ;

    & ul {
      display: flex;
    }

    & li {
      list-style: none;
      margin: 0 15px 0 15px;
      & svg {
        font-size: 1em;
      }
    }
  }

  &_copyright {
    display: flex;

    & ul {
      display: flex;
    }

    & li {
      list-style: none;
      margin: 0 3px 0 2px;
      & svg {
        font-size: 11px;
      }
    }

    & a {
      text-decoration: none;
      color: #fff;
      padding: 3px;
    }
  }

  &_links {
    li {
      position: relative;
    }

    a {
      color: #fff;
      text-decoration: none;
    }

    &_after::after {
      content: "";
      position: absolute;
      top: 50%;
      right: -15px;
      width: 1px;
      height: 1.07143rem;
      transform: translateY(-50%);
      border-right: 2px solid #7f817a;
    }
  }
}
